* {
  margin: 0;
}
body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.link_underline {
  text-decoration: none;
}


.logo__wrap {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 4px;
  margin-left: 16px;
}
.header__logo {
  width: 100px;
  object-fit: contain;

  margin-top: 15px;
}
.header__logoname {
  color: white;
}
.header {
  height: 52px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #131921;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__search {
  display: flex;
  flex: 0.9 1;
  align-items: center;
  border-radius: 24px;
}
.header__searchInput {
  padding: 10px;
  border: none;
  height: 12px;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #ffd814;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
.header__nav {
  display: flex;

  justify-content: space-between;
  align-items: center;

  margin-right: 14px;
}
.header__option {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-right: 12px;
  color: white;
}
.header__optionlineone {
  font-size: 10px;
  text-transform: capitalize;
}
.header__optionlinetwo {
  font-size: 13px;
  font-weight: 800;
}
.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
  margin-left: 5px;
}
.header__basketCount {
  margin-left: 5px;
}
@media screen and (max-width: 675px) {
  .header {
    height: 43px;
    width: auto;
  }
  .header__logo {
    width: 70px;
  }
  .header__optionlineone {
    font-size: 6px;
  }
  .header__optionlinetwo {
    font-size: 8px;
    font-weight: 800;
  }
  .header__searchInput {
    height: 6px;
  }
  .header__searchIcon {
    height: 16px !important;
  }
}

.home {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  /* max-width: 1500px; */
  margin-bottom: 35px;
}
.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
