/* carousel */
:root {
  --widths: 100%;
}

.slider-container {
  width: 100%;
  width: var(--widths);
  height: 220px;
  margin: auto;

  z-index: 2;
}

.active {
  display: inline-block;
}

.inactive {
  display: none;
}

.slides {
  width: 100%;
  width: var(--widths);
  position: relative;
}

.slide-image {
  width: 100%;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left 40%,
    left bottom,
    from(rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );
          mask-image: -webkit-gradient(
    linear,
    left 40%,
    left bottom,
    from(rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );

  object-fit: fill;
}

@media screen and (max-width: 675px) {
  .home__row {
    display: flex;
    flex-wrap: wrap;
  }

  .slider-container {
    height: 110px;
  }
  .prev,
  .next {
    position: absolute;
    top: 10%;

    font-size: 30px;
  }
  .slide-image {
    -webkit-mask-image: -webkit-gradient(
      linear,
      left 70%,
      left bottom,
      from(rgba(0, 0, 0, 1)),
      to(rgba(0, 0, 0, 0))
    );
            mask-image: -webkit-gradient(
      linear,
      left 70%,
      left bottom,
      from(rgba(0, 0, 0, 1)),
      to(rgba(0, 0, 0, 0))
    );
  }
  .slide-image {
    height: 150px;
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin: 8px;
  padding: 15px;
  width: 100%;

  flex: 1 1;
  min-width: 100px;
  background-color: white;
  z-index: 5;
  transition: 0.2s;
  border-radius: 2px;
}
.product:hover {
  box-shadow: 5px 10px 20px rgb(167, 212, 255);
}

.product__rating {
  display: flex;
}

.product > img {
  height: 170px;
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 1px;
}

.fbutton {
  background: #ffd814;
  outline: none;
  border: none;
  margin-top: 6px;
  font-size: smaller;
  padding: 4px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  cursor: pointer;
  border-radius: 4px;
}
.fbutton:focus {
  color: rgb(70, 70, 70);
  padding-left: 4px;
  padding-right: 4px;
}
.fbutton:hover {
  background: #ffe877;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media screen and (max-width: 675px) {
  .product {
    margin: 4px;
  }

  .product__price {
    margin-top: 2px;
  }
  .product__info {
    margin-bottom: 0px;
  }
  .fbutton {
    margin-top: 4px;
  }
  .product__rating {
    font-size: small;
  }
  .product > img {
    object-fit: contain;
  }
}

