.prev,
.next {
  display: none;
  cursor: pointer;
  z-index: 2;
  position: absolute;
  top: 20%;
  width: auto;
  padding: 1rem;

  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.prev:hover,
.next:hover {
  color: white;

  transition: all 0.2s ease-in;
}

.next {
  right: 0;
  border-radius: 5px 0px 0px 5px;
}

.slidersection {
  transition: 0.5s;
}

.checkout {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  background-color: white;
  height: -webkit-max-content;
  height: max-content;
  margin-bottom: 35px;
}
.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}
.checkout__title {
  margin-right: 10px;
  margin-bottom: 5px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}
.user__name {
  text-transform: capitalize;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin-left: 10px;
  font-size: large;
}
@media screen and (max-width: 675px) {
  .checkout {
    display: flex;
    flex-direction: column;
  }
}

.checkoutProduct {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 15px;
}
.checkoutProduct__info {
  padding-left: 20px;
}
.checkoutProduct__info > button {
  background: #ff9437;
  outline: none;
  border: none;
  width: 58px;
  margin-top: 10px;
  font-size: small;
  color: #111;
  padding: 3px;
  border-radius: 3px;
  margin-left: auto;
  margin-right: auto;
}
.checkoutProduct__info > button:hover {
  background: #ffe66c;
}
.checkoutProduct__image {
  object-fit: contain;
  width: 150px;
  height: 170px;
}
.checkoutProduct__rating {
  display: flex;
}
.checkoutProduct__title {
  font-weight: 800;
  font-size: 17px;
}
@media screen and (max-width: 675px) {
  .checkoutProduct__title {
    font-weight: 800;
    font-size: 12px;
  }
}

.subtotal {
  display: flex;
  outline: none;
  border: none;
  flex-direction: column;
  justify-content: space-between;
  width: 295px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border-radius: 3px;
  margin-left: 20px;
  margin-right: 5px;
  margin-top: 8px;
}
.subtotal__gift {
  display: flex;
  align-items: center;
}
.subtotal__gift > input {
  margin-right: 5px;
}
.subtotal > button {
  background: #ffd814;
  outline: none;
  border: none;
  border-radius: 6px;
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
@media screen and (max-width: 675px) {
  .subtotal {
    width: 300px;
    height: 85px;
    padding: 15px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 8px;
  }
}

.login {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}
.login__container {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}
.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.login__container > form > h5 {
  margin-bottom: 5px;
}
.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 3px 4px;
}

.login__signInButton {
  width: 100%;
  border-radius: 2px;
  background-color: #ffd814;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  margin-bottom: 5px;
  border: none;
  cursor: pointer;
}
.login__registerButton {
  width: 100%;
  border-radius: 2px;
  cursor: pointer;
  height: 30px;
  border: none darkgray;
  margin-top: 10px;
}
.login__logoutButton {
  width: 100%;
  border-radius: 2px;
  cursor: pointer;
  height: 30px;
  border: none rgb(250, 244, 244);
  margin-top: 10px;
}

